import React, { Fragment, useEffect, useRef } from "react";
import Moosix from "../functions/mossix-api.js"
import firebase from "firebase"
import { useState } from "react"

const app = new Moosix()
function Like(props){
    let cUID
    const uID = props.uID //sets userId
    let music = props.music
    let u_data;
    let cu_data;
    if(firebase.auth().currentUser){
        cUID=firebase.auth().currentUser.uid
    }
    var title = props.name //sets tractk title
    let userName = props.userName //sets userName
    let likeCheck; //only true or false
    const [likeNumber, setLikeNumber] = useState()
    const [like, setLike] = useState()
    const [counter, setCounter] = useState() 
        useEffect(()=>{  
            //goes to firebase and check if the value is true or undefined
            firebase.database().ref("users/"+cUID+"/private/feedback/byGenre").on("value", snapshot=>{
                if(snapshot.val()){
                    if(snapshot.val()[music.info.genre]){
                        setCounter(snapshot.val()[music.info.genre])
                    }else{
                        setCounter(0)
                    }
                }else{
                    setCounter(0)
                }
            })
            firebase.database().ref("users/"+uID+"/public/"+"songs/"+title+"/widgetInfo/likes/"+userName).on("child_added", (snapshot)=>{
                likeCheck = snapshot.val().liked//stores the value in it
                setLike(likeCheck)//sets the value in it
            })
            firebase.database().ref("users/"+uID+"/public/"+"songs/"+title+"/widgetInfo/likes/").on("value", (snapshot)=>{
                let likes = snapshot.val()
                let likeArr = []
                if(likes){
                    Object.values(likes).forEach((value)=>{ 
                        likeArr.push(likeArr)
                    })
                    setLikeNumber(likeArr.length)
                }
            })
        }, [userName])
        function sendLike (){
            //if like is true then it removes the child
            if(uID){
                if (like){
                    setLike(false)
                    setLikeNumber(likeNumber - 1)
                    firebase.database().ref("users/"+uID+"/public/"+"songs/"+title+"/widgetInfo/likes/"+userName).remove();
                    firebase.database().ref("public/songs/"+title+"/widgetsInfo/likes/"+userName).remove()
                    //genre counter 
                    firebase.database().ref("users/"+cUID+"/private/feedback/byGenre").update({ 
                        [music.info.genre]: counter-1
                    })
                    firebase.database().ref("users/"+cUID+"/private/feedback/liked/"+uID+"/"+title).remove()
                }
                //if like is undefined, then it adds the child with true value
                else{
                    setLike(true)
                    setLikeNumber(likeNumber + 1)
                    firebase.database().ref("users/"+cUID+"/private/feedback/liked/"+uID+"/"+title).set({
                        "id": uID,
                        "title": title
                    })
                         //genre counter 
                        firebase.database().ref("users/"+cUID+"/private/feedback/byGenre").update({ 
                            [music.info.genre]: counter +1
                        })
    
                    firebase.database().ref("users/"+uID+"/public/"+"songs/"+title+"/widgetInfo/likes/"+userName).push().set({
                        "userName": userName,
                        "liked": true
                    })
                    firebase.database().ref("public/songs/"+title+"/widgetsInfo/likes/"+userName).push().set({
                        "userName": userName,
                        "liked": true
                    })
                }
            }
        }
    return(
        <div class = "like">
            <i class="material-icons" onClick = {sendLike} >{like ? <p>favorite</p> : <p>favorite_border</p> }</i>
            <span>{likeNumber}</span>
        </div>
    )
}
export default Like